import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';

// import { ToolbarComponent } from './toolbar/index';
import { NavbarComponent } from './navbar/index';
import {CarouselComponent} from "./carousel/carousel.component";
import {ColumnHeaderComponent} from "./column-header/column-header.component";
import {FeatureHeaderComponent} from "./feature-header/feature-header.component";
import {FooterComponent} from "./footer/footer.component";

/**
 * Do not specify providers for modules that might be imported by a lazy loaded module.
 */

@NgModule({
  imports: [CommonModule, RouterModule],
  declarations: [NavbarComponent, CarouselComponent, ColumnHeaderComponent, FeatureHeaderComponent,
    FooterComponent],
  exports: [NavbarComponent, CommonModule, FormsModule, RouterModule, CarouselComponent,
    ColumnHeaderComponent, FeatureHeaderComponent, FooterComponent]
})
export class SharedModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedModule,
    };
  }
}
